import React from "react";
import "./header.scss";
import img from "../../images/Headerimg.JPG";

const Header = () => {
  return (
    <div className="header">
      <div className="container">
        <div className="display-div">
            <div className="left">
              <h1>
                  <br />   <br />   <br />
                
              </h1>
              <div className="bottom">
                <div className="start">
                  <button>  </button>
                  <a href="##">   </a>
                </div>
              </div>
            </div>
            <div className="right">
              <img src={img} alt="Header_image" />
            </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
