import React from "react";
import "./cources.scss";
import data from "./courcesData";

const Cources = () => {
  return (
    <div className="cources">
      <div className="container">
        <div className="c_content">
          <h1> </h1>
          <div className="cards">
            <div className="cardo">
              <div className="c_contents">
                <h2 className="acca_name">ACCA</h2>
                <p className="acca_p">
                  ACCA -     
                  ,   ,   .
                </p>
                <h3 className="acca_h3"> F1  F3 Paper</h3>
                <p className="acca_pr">2.700000/  paper  3 </p>
                <h3 className="acca_h3"> F4  F9 Paper</h3>
                <p className="acca_pr">3.100000/  paper  3 </p>
                <button className="button"></button>
              </div>
            </div>
            <div className="cardo">
              <div className="c_contents">
                <h2 className="acca_name">FRONT-END</h2>
                <p className="acca_p">
                  FrontEnd      
                  -,   .
                </p>
                <h3 className="acca_h3">7 </h3>
                <p className="acca_pr">1.500000/</p>
                <button className="button"></button>
              </div>
            </div>
            {data.map((el) => {
              return (
                <div className="card">
                  <div className="c_contents">
                    <h2 className="c_h2">{el.cource}</h2>
                    <p className="c_p">{el.content}</p>
                    <h3 className="c_h3">{el.science}</h3>
                    <p className="c_pr">{el.price}</p>
                    <h3 className="c_h3">{el.science2}</h3>
                    <p className="c_pr">{el.price2}</p>
                    <button className="button"></button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Cources;
