import React, { useEffect, useState } from "react";
import logo from "../../images/Logo.svg";
import "./navbar.scss";
import { NavLink, Link } from "react-router-dom";
import CloseIcon from "@mui/icons-material/Close";
import SideBar from "./SideBar";
import MenuIcon from '@mui/icons-material/Menu';

const Navbar = () => {
  // const [fixed, setFixed] = useState(false);

  // useEffect(() => {
  //   window.addEventListener("scroll", () => {
  //     if (window.scrollY > 50) {npm start
  
  //       setFixed(true); //navbar 100 dan keyin fixed boladi
  //     } else {
  //       setFixed(false);
  //     }
  //   });
  // });

  const [sidebar, setSidebar] = useState(false);

  const showSidebar = () => setSidebar(!sidebar);

  return (
    <nav className="navbar navbar-expand-lg navbar-light">
      <div class="container">
        <NavLink to="/" class="navbar-brand">
          <img src={logo} alt="Logo_brand" />
        </NavLink>
        <button
          class="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <div className="div"></div>
          <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <NavLink activeClassName="active-link" to="/ACCA">
                ACCA
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink activeClassName="active-link" to="/Front-end">
                IT Front-end
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink activeClassName="active-link" to="/Math">
                Math
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink activeClassName="active-link" to="/IELTS">
                IELTS
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink activeClassName="active-link" to="/General-English">
                General English
              </NavLink>
            </li>
          </ul>
          <a className="nav-item" href="tel:+998887012229">
            <button className="btn">+998 88 701 22 29</button>
          </a>
          <li className="sidebar" onClick={showSidebar}>
            <MenuIcon/>
          </li>
          <div className={sidebar ? "side-menu active" : "side-menu"}>
            <CloseIcon className="closeIcon" onClick={showSidebar} />
            <ul className="sideBarNav">
              {SideBar.map((item, index) => {
                return (
                  <li key={index} className="sideBarLinks">
                    <Link to={item.path}>
                      <ul>
                        <li>{item.title}</li>
                      </ul>
                    </Link>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
