import React from 'react'
import "./GE.scss"
import geH from "../../images/geH.png"

const GE = () => {
    return (
        <div>
            <div className="container">
                <div className="ge-header">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="ge-text">
                                <h1>General English</h1>
                                <h6>
                                      
                                      
                                        IELTS
                                     4 
                                </h6>
                                <button> </button>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="ge-img">
                                <div></div>
                                <img src={geH} alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default GE