import React from "react";
import Slider from "react-slick";
import data from "./teachersData";
import "./teachersSlick.scss";

const TeachersSlick = () => {
  var settings = {
    dots: true,
    infinite: false,
    speed: 1000,
    slidesToShow: 4,
    slidesToScroll: 4,
    initialSlide: 0,
    autoplay: true,
    autoplaySpeed: 2000,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <div className="container">
      <div className="ts">
        <Slider {...settings}>
          {data.map((el) => {
            return (
              <div className="card">
                <div className="card__body">
                  <img src={el.image} alt="" />
                  <div className="card__bottom">
                    <h4>{el.science}</h4>
                    <p>{el.name}</p>
                  </div>
                </div>
              </div>
            );
          })}
        </Slider>
      </div>
    </div>
  );
};

export default TeachersSlick;
