import React from 'react'
import "./IeltsHeader.scss"
import IeltsH from "../../images/IeltsH.png"
const IeltsHeader = () => {
    return (
        <div>
            <div className="container">
                <div className="ielts-header">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="ielts-text">
                                <h1>IELTS</h1>
                                <h6>
                                      IELTS
                                      
                                      
                                     IELTS
                                         MOCK
                                </h6>
                                <button> </button>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="ielts-img">
                                <div></div>
                                <img src={IeltsH} alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default IeltsHeader