import React from 'react'
import "./MathHeader.scss"
import mathH from "../../images/mathH.png"

const MathHeader = () => {
    return (
        <div>
            <div className="container">
                <div className="math-header">
                    <div className="row">
                        <div className="col-md-6">
                            <div className="math-text">
                                <h1>Math</h1>
                                <h6>
                                       
                                       
                                          
                                      
                                </h6>
                                <button> </button>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <div className="math-img">
                                <div></div>
                                <img src={mathH} alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MathHeader